.sidebar {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  text-align: center;
  position: absolute;
  top: 0px;
  left: -200px;
  width: 200px;
  height: 100%;
  background-color: #454545;
  color: orange;
  transition: all, 0.3s ease-in-out;
  z-index: -1;
}

.sidebar .click {
  cursor: pointer;
  border-color: red;
  padding-top: 10px;
  padding-bottom: 10px;
  height: 20px;
  background-color: white;
  border-style: solid;
  border-width: 1px;
}

.sidebar-open .click {
  cursor: pointer;
  border-color: red;
  padding-top: 10px;
  padding-bottom: 10px;
  height: 20px;
  background-color: white;
  border-style: solid;
  border-width: 1px;
}
.content {
  text-align: center;
  align-items: center;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: -1;

  transition: all, 0.3s ease-in-out;
}

.content-open {
  text-align: center;
  align-items: center;
  position: absolute;
  top: 0px;
  left: 100px;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: all, 0.3s ease-in-out;
}
.sidebar-open {
  display: flex;
  flex-direction: column;
  font-size: large;
  /* justify-content: center; */
  text-align: center;
  position: absolute;
  /* gap: 20px; */
  left: 0px;
  top: 0px;
  width: 200px;
  height: 100%;
  background-color: #454545;
  transition: all, 0.3s ease-in-out;
  color: green;
  z-index: -1;
}
